import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);


let postService = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/'
})

let apiPostBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/lms/User/'
})
let apiGetBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/lms/User/'
})

export default {
    getPublicKey: (opts) => {
        return apiPostBaseUrl({ url: 'key?', data: { ...opts } })
    },
    login: (opts) => {
        return postService({ url: 'j_spring_security_check?', params: { ...opts } })
    },
    logout: () => {
        return postService({ url: 'logout' })
    },
    hasLogged: () => {
        return apiPostBaseUrl({ url: 'hasLogged' })
    },
    getPhoneVerificationCode: (opts) => {
        return apiPostBaseUrl({ url: 'getPhoneVerificationCode', data: { ...opts } })
    },
    resetPasswordByPhone:(opts) => {
        return apiPostBaseUrl({ url: 'resetPasswordByPhone', data: { ...opts } })
    }

}