let router = new Router({
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return {
        x: 0,
        y: 0
      };
    } else {
      return savedPosition;
    }
  }
});

export default router;

router.beforeEach((to, from, next) => {
  setTitle(to, from, next);
  ctrlNavBar(to, from, next);
  ctrlTabBar(to, from, next);
  redirectLogin(to, from, next);
  next();
});

function redirectLogin(to, from, next) {
  let requireAuth = to.meta.requireAuth;
  let toPath = to.path;

  userAPI.hasLogged().then(res => {
    let hasLogged = res.data.result.hasLogged;
    if (hasLogged) {
      //已经登录，不能跳转登录页
      if (toPath === '/login') {
        next({ path: '/' });
      }
    } else if (requireAuth) {
      //没有登录，并且需要登录
      next({ path: '/login', query: { redirect: to.fullPath } });
    }
  });
}

function ctrlTabBar(to, from, next) {
  if (to.meta.navName) {
    //需要navBar的页面
    vuex.dispatch('AgettabberSwite', true);
    vuex.dispatch('AgetnavbarText', to.meta.navName);
  } else {
    vuex.dispatch('AgettabberSwite', false);
  }
}
function ctrlNavBar(to, from, next) {
  if (to.meta.hasnav) {
    // 含有标题栏
    vuex.dispatch('AgetnavberSwite', true);
    vuex.dispatch('AgettitleText', to.meta.title);
    if (to.meta.getshowBack) {
      // 不含有返回按钮
      vuex.dispatch('AgetshowBack', false);
    } else {
      vuex.dispatch('AgetshowBack', true);
    }
    if (to.meta.rightBtnText) {
      // 含有右侧文字按钮
      vuex.dispatch('AgetrightBtnText', to.meta.rightBtnText);
    } else {
      vuex.dispatch('AgetrightBtnText', '');
    }
    if (to.meta.url) {
      // 右侧按钮链接
      vuex.dispatch('AgetnavUrl', to.meta.url);
    } else {
      vuex.dispatch('AgetnavUrl', 'javascript:;');
    }
    if (to.meta.leftBtn) {
      // 左侧按钮为文字
      vuex.dispatch('AgetleftBtn', to.meta.leftBtn);
    } else {
      vuex.dispatch('AgetleftBtn', '');
    }
    if (to.meta.changeColor) {
      // 改变标题栏底色为主题色
      vuex.dispatch('AgetchangeColor', to.meta.changeColor);
    } else {
      vuex.dispatch('AgetchangeColor', false);
    }
    if (to.meta.rightIcon) {
      // 含有右侧iconfont按钮
      vuex.dispatch('AgetrightIcon', to.meta.rightIcon);
    } else {
      vuex.dispatch('AgetrightIcon', false);
    }
  } else {
    vuex.dispatch('AgetnavberSwite', false);
  }
}

function setTitle(to, from, next) {
  if (to.meta.title) {
    document.title = to.meta.title;
    // 如果是 iOS 设备，则使用如下 hack 的写法实现页面标题的更新
    let str = /\(i[^;]+;( U;)? CPU.+Mac OS X/;
    if (navigator.userAgent.match(str)) {
      const hackIframe = document.createElement('iframe');
      hackIframe.style.display = 'none';
      hackIframe.src = '/fixIosTitle.html?r=' + Math.random();
      document.body.appendChild(hackIframe);
      setTimeout(_ => {
        document.body.removeChild(hackIframe);
      }, 300);
    }
  }
}
