const state = {
  user:null,
  memberNumber:''
};

const getters = {
  user: state => state.user,
  memberNumber: state => state.memberNumber
};
const actions = {
  // setUser: ({ commit }, data) => commit('setLoginUser', data)
  setUser({ commit }, data) {
    commit('setLoginUser', data)
  },
  setMemberNumber({ commit }, data) {
    commit('setMemberNumber', data)
  }
};

const mutations = {
  setLoginUser(state, inner) {
    state.user = inner;
  },
  setMemberNumber(state, inner) {
    state.memberNumber = inner;
  },
};

export default {
  state,
  getters,
  actions,
  mutations
};
